@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", Georgia, Times, "Times New Roman", serif;
}

.pop_up_wrapper {
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
}
.pop_up_wrapper .pop_up {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: auto 1rem;
  background-color: #fff;
  padding: 1rem 2rem;
}
.pop_up_wrapper .close_btn_wrapper {
  display: flex;
  justify-content: flex-end;
  color: black;
}
.pop_up_wrapper .close_btn_wrapper .close_btn {
  background: transparent;
  border: none;
  font-size: 1.5rem;
  transition: transform ease-in-out 0.3s;
  padding: 0.5rem 0.5rem 0 0.5rem;
}
.pop_up_wrapper .close_btn_wrapper .close_btn:hover {
  transform: scale(1.5);
}
.pop_up_wrapper .reset_btn_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pop_up_wrapper .reset_btn_wrapper .reset_btn {
  background-color: tomato;
  color: #fff;
  border: tomato;
  padding: 1rem 2rem;
  border-radius: 32px;
  transition: transform ease-in-out 0.3s;
}
.pop_up_wrapper .reset_btn_wrapper .reset_btn:hover {
  transform: scale(1.1);
}

.content_wrapper {
  max-width: 1440px;
  margin: 5rem 1rem;
}
.content_wrapper img {
  width: 100%;
  height: auto;
}
.content_wrapper .content_text {
  margin-top: 1rem;
}
.content_wrapper .content_text p {
  margin: 1rem 0;
}
.content_wrapper .content_text .content_pop_up_btn {
  background-color: #e4c1ae;
  color: #fff;
  border: #fff;
  padding: 1rem 2rem;
  border-radius: 32px;
  transition: transform ease-in-out 0.3s;
}
.content_wrapper .content_text .content_pop_up_btn:hover {
  transform: scale(1.1);
}